// {"title":"Markdown","description":"Information about the Markdown generator.","audience":"Everyone","level":"Beginner","section":"Generators","link":"/basics/02-markdown","source":"/home/runner/work/pidoc/pidoc/packages/example-app/docs/generators/02-markdown.md"}
import * as React from 'react';
import { ContentPage } from '@pidoc/components';

    import { PageContent, Markdown, PageLayout } from '@pidoc/components';

    const link = "https://github.com/smapiot/pidoc/tree/develop/packages/example-app/docs/generators/02-markdown.md";
    const html = `
<h1 id="markdown-generator">Markdown Generator</h1>
<p>The Markdown generator transforms single Markdown files into documentation pages. For this process <code>markdown-it</code> is used.</p>
<p>The following plugins are configured:</p>
<ul>
<li><code>markdown-it-abbr</code>, to add abbreviations</li>
<li><code>markdown-it-anchor</code>, to allow named anchors</li>
<li><code>markdown-it-attrs</code>, to add custom attributes</li>
<li><code>markdown-it-div</code>, to have flexible container divs</li>
<li><code>markdown-it-emoji</code>, to be able to write emojis</li>
<li><code>markdown-it-footnote</code>, to write foot notes</li>
<li><code>markdown-it-front-matter</code>, to ignore / extract front matter</li>
<li><code>markdown-it-highlightjs</code>, for code highlighting</li>
<li><code>markdown-it-include</code>, for file content inclusions</li>
<li><code>markdown-it-container</code>, for dynamic fences</li>
<li><code>markdown-it-mark</code>, to include mark</li>
<li><code>markdown-it-task-checkbox</code>, for task-list-like checkboxes</li>
<li><code>markdown-it-replace-link</code>, to replace links with the correct URL</li>
<li><code>markdown-it-smartarrows</code>, to render nicer arrows</li>
<li><code>markdown-it-sub</code>, to support sub-level notations</li>
<li><code>markdown-it-sup</code>, to support super-level notations</li>
<li><code>markdown-it-video</code>, to include videos nicely</li>
</ul>
<p>Most of the (supported / included) Markdown features are describes in the <a href="/basics/03-markdown">Markdown section</a>.</p>
<p>The Markdown generator is pretty much the default generator as most content will most likely be written using Markdown. Let's say you have a folder structure like this:</p>
<pre><code class="hljs language-plain">docs/
docs/foo/content.md
docs.config.json
</code></pre>
<p>A sitemap section in <em>docs.config.json</em> to cover this would be:</p>
<pre><code class="hljs language-json"><span class="hljs-punctuation">{</span>
  <span class="hljs-comment">// ...</span>
  <span class="hljs-attr">&quot;sitemap&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
    <span class="hljs-attr">&quot;example&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
      <span class="hljs-attr">&quot;title&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Example&quot;</span><span class="hljs-punctuation">,</span>
      <span class="hljs-attr">&quot;sections&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">[</span>
        <span class="hljs-punctuation">{</span>
          <span class="hljs-attr">&quot;generator&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;markdown&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;segment&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;dir&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;foo&quot;</span>
        <span class="hljs-punctuation">}</span>
      <span class="hljs-punctuation">]</span>
    <span class="hljs-punctuation">}</span>
  <span class="hljs-punctuation">}</span>
<span class="hljs-punctuation">}</span>
</code></pre>
<p>This will look up the <em>.md</em> files in the <code>foo</code> directory below the <code>docs</code> documentation root directory.</p>
`;
    const meta = {"title":"Markdown","description":"Information about the Markdown generator.","audience":"Everyone","level":"Beginner","section":"Generators"};
  

export default () => (
  <ContentPage>
    
    <PageLayout name={"default"} meta={meta}>
      <PageContent meta={meta}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    </PageLayout>
  
  </ContentPage>
);